.container {
    text-align: center;
  }
  
  .banner {
    display: flex;
    align-items: center;
    justify-content: center;
    margin-bottom: 60px;
  }
  
  .banner-image {
    width: 100px; 
    height: 100px; 
    margin-right: 10px;
  }
  

  .banner-text {
    font-family: 'Vinque', sans-serif; 
    font-size: 2rem; 
    color: #e7e7e7; 
    text-align: left;
    white-space: pre-line; 
  }
  

  